import React, { useEffect, useState } from 'react';
import { getDiscountCodes } from '../utils/helpers';
import { useAuth } from '../context/AuthContext';

function formatDate(dateStr) {
  const d = new Date(dateStr);
  return d.toLocaleDateString();
}

const DiscountCodesPage = () => {
  const [codes, setCodes] = useState([]);
  const { user } = useAuth();

  useEffect(() => {
    if (user) setCodes(getDiscountCodes(user.id));
  }, [user]);

  const now = new Date();

  return (
    <div className="max-w-2xl mx-auto p-4">
      <h2 className="text-2xl font-bold mb-4">M gim gi ca bn</h2>
      {codes.length === 0 ? (
        <p>Bn cha c m gim gi no.</p>
      ) : (
        <ul className="space-y-4">
          {codes.map((c) => {
            const expired = new Date(c.expiresAt) < now;
            const used = c.used;
            const isActive = !used && !expired;
            return (
              <li
                key={c.code}
                className={`border rounded p-4 flex flex-col md:flex-row md:items-center justify-between transition-opacity ${!isActive ? 'opacity-50 bg-gray-100' : 'bg-white'}`}
              >
                <div>
                  <div className="font-mono text-lg font-semibold">{c.code}</div>
                  <div>Gi tr: <span className="font-bold">{c.discountValue.toLocaleString()}</span></div>
                  <div>Hn s dng: {formatDate(c.expiresAt)}</div>
                </div>
                <div className="mt-2 md:mt-0">
                  {used ? (
                    <span className="text-gray-500"> s dng</span>
                  ) : expired ? (
                    <span className="text-red-500">Ht hn</span>
                  ) : (
                    <span className="text-green-600 font-bold">Cn hiu lc</span>
                  )}
                </div>
              </li>
            );
          })}
        </ul>
      )}
    </div>
  );
};

export default DiscountCodesPage; 