import React, { createContext, useContext, useState, useEffect } from 'react';

const AuthContext = createContext();

export const useAuth = () => {
  const context = useContext(AuthContext);
  if (!context) {
    throw new Error('useAuth must be used within an AuthProvider');
  }
  return context;
};

export const AuthProvider = ({ children }) => {
  const [user, setUser] = useState(null);
  const [loading, setLoading] = useState(true);

  // Check if user is logged in on app start
  useEffect(() => {
    const savedUser = localStorage.getItem('user');
    if (savedUser) {
      setUser(JSON.parse(savedUser));
    }
    setLoading(false);
  }, []);

  // Login function
  const login = async (email, password) => {
    try {
      // Admin hardcoded
      if (email === 'Admin@gmail.com' && password === 'Aa123456@') {
        // Nu  c profile admin trong localStorage th ly ra
        const adminProfile = localStorage.getItem('admin_profile');
        let adminUser;
        if (adminProfile) {
          adminUser = JSON.parse(adminProfile);
        } else {
          adminUser = {
            id: 'admin',
            email: 'Admin@gmail.com',
            name: 'Administrator',
            role: 'admin'
          };
        }
        setUser(adminUser);
        localStorage.setItem('user', JSON.stringify(adminUser));
        return { success: true, user: adminUser };
      }
      // Check regular user credentials (demo purposes)
      const users = JSON.parse(localStorage.getItem('users') || '[]');
      const foundUser = users.find(u => u.email === email && u.password === password);
      if (foundUser) {
        const userWithoutPassword = { ...foundUser };
        delete userWithoutPassword.password;
        setUser(userWithoutPassword);
        localStorage.setItem('user', JSON.stringify(userWithoutPassword));
        // Gi thng bo cho mng/mo ch 1 ln duy nht cho mi user
        if (!localStorage.getItem(`welcome_noti_${userWithoutPassword.id}`)) {
          addNotification(userWithoutPassword.id, 'Cho mng bn n vi ClothingStore!  Vui lng cp nht thm thng tin a ch v s in thoi ca bn  d dng t hng.');
          localStorage.setItem(`welcome_noti_${userWithoutPassword.id}`, '1');
        }
        if (!localStorage.getItem(`tip_noti_${userWithoutPassword.id}`)) {
          addNotification(userWithoutPassword.id, ' Mo: Bn c th lu a ch giao hng  t hng nhanh hn!');
          localStorage.setItem(`tip_noti_${userWithoutPassword.id}`, '1');
        }
        return { success: true, user: userWithoutPassword };
      }
      return { success: false, message: 'Email hoc mt khu khng ng' };
    } catch (error) {
      return { success: false, message: ' c li xy ra' };
    }
  };

  // Register function (only allow user role)
  const register = async (userData) => {
    try {
      await new Promise(resolve => setTimeout(resolve, 1000));
      const users = JSON.parse(localStorage.getItem('users') || '[]');
      if (users.some(u => u.email === userData.email) || userData.email === 'Admin@gmail.com') {
        return { success: false, message: 'Email  c s dng' };
      }
      const newUser = {
        id: Date.now().toString(),
        ...userData,
        role: 'user'
      };
      users.push(newUser);
      localStorage.setItem('users', JSON.stringify(users));
      const userWithoutPassword = { ...newUser };
      delete userWithoutPassword.password;
      setUser(userWithoutPassword);
      localStorage.setItem('user', JSON.stringify(userWithoutPassword));
      // Thng bo cho mng/mo ch to 1 ln
      localStorage.setItem(`welcome_noti_${userWithoutPassword.id}`, '1');
      localStorage.setItem(`tip_noti_${userWithoutPassword.id}`, '1');
      addNotification(userWithoutPassword.id, 'Cho mng bn n vi ClothingStore!  Vui lng cp nht thm thng tin a ch v s in thoi ca bn  d dng t hng.');
      addNotification(userWithoutPassword.id, ' Mo: Bn c th lu a ch giao hng  t hng nhanh hn!');
      return { success: true, user: userWithoutPassword };
    } catch (error) {
      return { success: false, message: ' c li xy ra' };
    }
  };

  // Logout function
  const logout = () => {
    setUser(null);
    localStorage.removeItem('user');
  };

  // Update profile function
  const addNotification = (userId, message, link = null) => {
    const key = `notifications_${userId}`;
    const notifications = JSON.parse(localStorage.getItem(key) || '[]');
    notifications.unshift({
      id: Date.now(),
      message,
      link,
      read: false,
      time: new Date().toISOString(),
    });
    localStorage.setItem(key, JSON.stringify(notifications));
  };

  const updateProfile = async (userData) => {
    try {
      if (!user) return { success: false, message: 'Cha ng nhp' };
      let isPasswordChange = !!userData.password;
      if (user.id === 'admin') {
        const updatedAdmin = { ...user, ...userData };
        setUser(updatedAdmin);
        localStorage.setItem('user', JSON.stringify(updatedAdmin));
        localStorage.setItem('admin_profile', JSON.stringify(updatedAdmin));
        addNotification(updatedAdmin.id, isPasswordChange ? 'Bn  i mt khu thnh cng.' : 'Bn  cp nht thng tin ti khon thnh cng.');
        return { success: true, user: updatedAdmin };
      }
      const users = JSON.parse(localStorage.getItem('users') || '[]');
      const userIndex = users.findIndex(u => u.id === user.id);
      if (userIndex === -1) {
        return { success: false, message: 'Khng tm thy ngi dng' };
      }
      const prevUser = users[userIndex];
      const updatedUser = { ...users[userIndex], ...userData };
      users[userIndex] = updatedUser;
      localStorage.setItem('users', JSON.stringify(users));
      const userWithoutPassword = { ...updatedUser };
      delete userWithoutPassword.password;
      setUser(userWithoutPassword);
      localStorage.setItem('user', JSON.stringify(userWithoutPassword));
      
      // Kim tra trng no  cp nht
      const changedFields = [];
      if (userData.phone && userData.phone !== prevUser.phone) changedFields.push('s in thoi');
      if (userData.address && userData.address !== prevUser.address) changedFields.push('a ch');
      if (userData.city && userData.city !== prevUser.city) changedFields.push('thnh ph');
      if (userData.country && userData.country !== prevUser.country) changedFields.push('quc gia');
      // Kim tra trng no cn thiu
      const missingFields = [];
      if (!userWithoutPassword.phone) missingFields.push('s in thoi');
      if (!userWithoutPassword.address) missingFields.push('a ch');
      if (!userWithoutPassword.city) missingFields.push('thnh ph');
      if (!userWithoutPassword.country) missingFields.push('quc gia');
      let message = '';
      if (isPasswordChange) {
        message = 'Bn  i mt khu thnh cng.';
      } else if (changedFields.length > 0) {
        message = `Bn  cp nht thnh cng ${changedFields.join(', ')}.`;
        if (missingFields.length > 0) {
          message += ` Hy b sung thm (${missingFields.join(', ')})  d dng t hng qua trang web.`;
        } else {
          message = 'Bn  cp nht y  thng tin ti khon thnh cng!';
        }
      } else {
        message = 'Bn  cp nht thng tin ti khon.';
      }
      addNotification(userWithoutPassword.id, message);
      return { success: true, user: userWithoutPassword };
    } catch (error) {
      return { success: false, message: ' c li xy ra' };
    }
  };

  const value = {
    user,
    login,
    register,
    logout,
    updateProfile,
    loading,
    isAuthenticated: !!user,
    isAdmin: user?.role === 'admin'
  };

  return (
    <AuthContext.Provider value={value}>
      {!loading && children}
    </AuthContext.Provider>
  );
};