import React from 'react';
import { useCart } from '../../context/CartContext';
import { Link } from 'react-router-dom';

const CartSummary = () => {
  const { getCartTotal, getCartItemCount } = useCart();

  const subtotal = getCartTotal();
  const shipping = subtotal >= 500000 ? 0 : 30000;
  const total = subtotal + shipping;

  return (
    <div className="bg-white rounded-2xl shadow-lg p-6 sticky top-4">
      <h3 className="text-lg font-semibold text-gray-800 mb-4">Tm Tt n Hng</h3>
      <div className="space-y-2">
        <div className="flex justify-between">
          <span className="text-gray-600">S lng sn phm:</span>
          <span className="font-medium">{getCartItemCount()}</span>
        </div>
        <div className="flex justify-between">
          <span className="text-gray-600">Tm tnh:</span>
          <span className="font-medium">{subtotal.toLocaleString()}</span>
        </div>
        <div className="flex justify-between">
          <span className="text-gray-600">Ph vn chuyn:</span>
          <span className="font-medium">{shipping.toLocaleString()}</span>
        </div>
        <div className="flex justify-between pt-2 border-t border-gray-200">
          <span className="text-lg font-semibold text-gray-800">Tng cng:</span>
          <span className="text-lg font-bold text-blue-600">{total.toLocaleString()}</span>
        </div>
      </div>
      <Link
        to="/checkout"
        className="block w-full bg-blue-600 text-white py-3 rounded-lg font-semibold text-center hover:bg-blue-700 transition-colors mt-4"
      >
        Tin Hnh Thanh Ton
      </Link>
    </div>
  );
};

export default CartSummary;